html, body, #root, .App {
  background-color: lightblue;
  margin: 0px;
  padding: 0px;
  height: 100%;

}

.App {
  text-align: center;
}

#About, #Projects, #Contact, #Resume {
  width: fit-content;
  box-sizing: border-box;
  border-radius: 15px;
  background-color: lightpink;
  padding: 20px;
  margin: 20px auto;
}

#Resume, #Contact {
  text-align: center;
}
